.cab{
  margin: 10px;  
  display: flex;
  height: 600px;
  width: 500px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 10px;
  box-shadow: 0px 5px 5px 0px #aeaeae;
}
.cab:hover{
  box-shadow: 5px 5px 5px 5px #aeaeae;
}

.pic{
    height: 280px;
    width: 400px;
    border-radius: 10px;
  }
  .looding{
    display: flex;
    flex-direction: column;
    letter-spacing: 1px;
    font-family: monospace;
    justify-content: center;
    align-items: center;
    margin: 10px;
    background-color: rgba(60, 44, 52, 0.8);
    color: white;
    border: 1px solid black;
    border-radius: 10px;
    border-width: 0px;
  }
  .am{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .design{
    display: flex;
    flex-direction: column;
  }
  .tons{
    display: flex;
    flex-direction: column;
  }
  .map{
    padding: 10px;
    margin: 10px;
    background-color: lightgreen;
    color: black;
    border: 1px solid black;
    border-radius: 10px;
    border-width: 0px;
    text-align: center;
    font-size: 20px;
  }
  @media (max-width:991px) {
    .cab{
        margin: 20px;  
        display: flex;
        height: 600px;
        width: 300px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;
        border-radius: 10px;
        box-shadow: 0px 5px 5px 0px #aeaeae;
      }
      
   .pic{
      height: 280px;
      width: 200px;
      border-radius: 10px;
    }
    .map{
        margin: 20px;
    }
  }