.navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    box-shadow: 0px 5px 5px 0px #aeaeae;
    
}
.inside-links{
    display: flex;
    margin: 10px;
}

ul li{
    list-style: none;
    padding: 10px;
    margin: 10px;
  }
  ul li a{
    text-decoration: none;
  }
  a{
    text-decoration: none;
    padding: 10px;
    margin: 10px;
    color: black;
    letter-spacing: 1px;
    font-family: monospace;
  }
  .active{
    color: #fff;
    background-color: rgba(77, 54, 66, 0.82);
    border: 1px solid black;
    border-radius: 4px;
    border-width: 0px;
  }
@media (max-width:991px) {
    .inside-links{
        display: flex;
        margin: 10px;
    }

}