
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.cards{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr   ;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.card{
  display: flex;
  height: 400px;
  width: 400px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 10px;
  box-shadow: 0px 5px 5px 0px #aeaeae;
  margin-top: 100px;
  transition: all 0.8s;
}
.card:hover{
  transform: translateY(-40px);
  box-shadow: 5px 5px 5px 5px #aeaeae;
}
.pics{
  height: 300px;
  width: 300px;
  border-radius: 10px;
}
.loding{
  display: flex;
  justify-content: center;
  align-items: center;
}
.paru{
  padding: 10px;
  margin: 10px;
  border: 1px solid black;
  border-radius: 4px;
  background-color: lightslategray;
  color: #fff;
  border-width: 0px;
}
form{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 10px;
}
.footer{
  margin-top: 30px;
  display: flex;
  justify-content:center;
  align-items: center;
  height: 30vh;
  box-shadow: 5px 5px 0px 5px #aeaeae;
}
.footer-card{
  display: flex;
  flex-direction: column;
}
.icons{
  display: flex;
  justify-content:center;
  align-items: center;
  margin: 10px;
}
.style{
  padding: 10px;
  margin: 10px;
}

@media (max-width:991px) {
  .cards{
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }
  .cards{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .card{
    display: flex;
    height: 350px;
    width: 350px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 10px;
  }
  .pics{
    height: 200px;
    width: 200px;
    border-radius: 10px;
  }
  .copy{
    font-size: 10px;
  }
  .footer{
    margin-top: 100px;
    height: 20vh;
  }
}